import faqImage from 'assets/FAQs.png'
import { GetInTouch } from 'components/GetInTouchForn'
import { Button } from 'reactstrap'
import {withRouter} from 'react-router';

export const FAQs = () => {
    return (
        <div className="faq-page">
            <div className="row">
                <div className="col-4 m-auto">
                    <img src={faqImage} className='img-fluid' alt='' />
                </div>
            </div>
            <div className='mt-5 border faq-box'>
                <div className='bg-primary text-white text-center p-2'>General FAQs</div>
                <div className='p-3 faq-container'>
                    {new Array(6).fill(0).map((_, i) => (
                        <div className={`p-3 ${i%2 ? 'bg-light-gray' : 'bg-white'}`}>
                        </div>
                    ))}
                </div>
            </div>
            <div className='mt-5 row'>
                <div className='col-4'>
                    <div className='bg-primary rounded text-white text-center p-2'>Medical Content</div>
                </div>
                <div className='col-12'>
                    <div className='border rounded p-3'>
                        <div className='d-flex justify-content-end mt-3'>
                            <a size='sm' href='/' className='bg-primary text-white rounded-3 px-5 py-0'>See All</a>
                        </div>
                    </div>
                </div>
            </div>
            <div className='mt-5 row'>
                <div className='col-4'>
                    <div className='bg-primary rounded text-white text-center p-2'>Technical</div>
                </div>
                <div className='col-12'>
                    <div className='border rounded p-3'>
                        <div className='d-flex justify-content-end mt-3'>
                            <a size='sm' href='/' className='bg-primary text-white rounded-3 px-5 py-0'>See All</a>
                        </div>
                    </div>
                </div>
            </div>
            <div className='mt-5 row'>
                <div className='col-3 m-auto'>
                    <Button color='dark' outline block>Download the App</Button>
                </div>
            </div>
            <div className='mt-5 row'>
                <div className='col-3 m-auto'>
                    <Button color='dark' outline block>Share the App</Button>
                </div>
            </div>
            <GetInTouch />
        </div>
    )
}
export default withRouter(withRouter);