import React,{useState} from 'react';
import { Button, Modal, ModalBody } from "reactstrap";
import crossIcon from './../assets/Group 2910.png';

const ChangeCourseModal = (props) => {
    const {isOpen,closeModal} = props;
    const [key,setKey] = useState(0);
    const upper = [{key: 0, value:"NEET PG"},{key: 1, value: "FMGE"},{key:2,value: "NEET SS"}];
    const lower = [{key:3,value: "PG Residency" },{key:4,value:"MBBS Curriculum"}];
  return (
    <div>
        <Modal isOpen={isOpen} centered size="lg" toggle={closeModal}>
            <ModalBody className='p-0 pt-2'>
                <div className='p-0'>
                    <img src={crossIcon} alt='' className="cross-icon pb-2" height={20} role='button' onClick={closeModal} />
                    <div className="text-center py-1 m-0">
                        <h2 className='m-0'>Choose your dream</h2>
                    </div>
                </div>
                <hr className='mt-0'/>
                <div className='d-flex justify-content-center mb-0'>
                    {upper.map((item)=>(
                        <div>
                            {key == item.key?<button onClick={()=>setKey(item.key)} className='btn btn-primary m-3'>{item.value}</button>
                            :
                            <button onClick={()=>setKey(item.key)} className='btn btn-outline-secondary m-3'>{item.value}</button>
                            }
                       </div>
                    ))}
                   
                </div>
                <div className='d-flex justify-content-center mt-0'>
                {lower.map((item)=>(
                        <div>
                            {key == item.key?<button onClick={()=>setKey(item.key)} className='btn btn-primary m-3'>{item.value}</button>
                            :
                            <button onClick={()=>setKey(item.key)} className='btn btn-outline-secondary m-3'>{item.value}</button>
                            }
                       </div>
                    ))}
                </div>
                <div className='d-flex justify-content-center my-4'>
                    <div>
                        <button onClick={closeModal} className='btn btn-blue'>PROCEED</button>
                    </div>
                </div>

            </ModalBody>
        </Modal>
    </div>
  )
}

export default ChangeCourseModal