import React, { useState, useRef } from 'react';
import { Button, Progress } from "reactstrap";
import gridicon from 'assets/Grid View.png';
import listicon from 'assets/list.png';
import { useEffect } from 'react';
import { connect } from 'react-redux';
import {getQB_Anatomy} from './../../store/dashbaord/anatomy/action';
import ReactPlayer from 'react-player'
import {AiFillCloseCircle, AiFillPlayCircle} from 'react-icons/ai';
import { useRouteMatch, Link } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import {
  AccordionBody,
  AccordionItem,
  AccordionHeader,
  UncontrolledAccordion,
  Input,
  FormGroup,
  Form,
  UncontrolledTooltip  ,
  Label
} from "reactstrap";



export const ExamReview = () => {
  return (
    <div className="exam-review-wrapper">
      <div className="row">
        <div className="col-lg-12 title">
          Bones, Joints and Muscles of Upper Limb
        </div>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-lg-4 box">
              <div className="text-center">Total MCQs: 50</div>
              <div className="row h-90 align-items-center">
                <div className="col-lg-7 chart-wrapper"></div>
                <div className="col-lg-5">
                  <div className="wrapper">
                    <span className="correct"></span>
                    Correct
                  </div>
                  <div className="wrapper">
                    <span className="wrong"></span>
                    Wrong
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-3 box skipped">
              <div className="text-center">Skipped</div>
              <div className="prgress-wrapper">
                <div className="text-right label">
                  41/49
                </div>
                <Progress value={50} />
              </div>
              <div className="text-center">
                <Button>View Skipped Question</Button>
              </div>
            </div>
            <div className="col-lg-4 box">
              <div className="row text-center">
                <div className="col-lg-6 separator bottom">
                  <div className="percent">2%</div>
                  <div>Accuracy</div>
                </div>
                <div className="col-lg-6 separator bottom left">
                  <div className="percent">01:20</div>
                  <div>Total Time</div>
                </div>
                <div className="col-lg-6 separator">
                  <div className="percent">2s/Q</div>
                  <div>Speed</div>
                </div>
                <div className="col-lg-6 separator left">
                  <div className="percent">5s</div>
                  <div>Accuracy</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg-12">
          <div className="row btn-wrapper justify-content-center text-center">
            <div className="col-lg-2 btn-type">Home</div>
            <div className="col-lg-2 btn-type">Question Bank</div>
            <div className="col-lg-2 btn-type">review</div>
          </div>
        </div>
      </div>
    </div>
  );
};
const mapSateToProps = (state)=>({
  authState: state.authReducer,
  qstnState: state.dashBoardAnatomyReducer
})
const mapDispatchToProps = (dispatch)=>({
  fetchQB_Anatomys: (body)=>dispatch(getQB_Anatomy(body))
})
export default ExamReview;