import { email } from 'utils/validators'
import { Button, Input } from 'reactstrap';
import { Form, Field } from 'react-final-form';
import { V, P } from 'utils';
import { connect, useDispatch } from 'react-redux';
import {stateList, stateCollegeMap, courseList, getCourseIdByName, getCourseNameById,yearCourseMap} from './../../utils/methods'; 
import createDecorator from 'final-form-calculate'
import { useState,useEffect } from 'react';
import { axiosRequestSilent } from 'store/apis/api-v1';
import { toast } from 'react-toastify';
import { logout, syncProfile } from 'store/authentication/action';
import { resendEmailVerificationLink,deleteAccount } from 'store/dashbaord/profile/action';
import { AiTwotoneWarning } from "react-icons/ai";
import UploadImageModal from 'components/UploadImageModal';
import camera from './../../assets/camera.png'
import manicon from './../../assets/Group 9547.png'


export const Profile = (props) => {

    const {profileInfo} = props.authState;
    const {requestSend,setRequestSend} = props;
    const [initialState,setInitialState] = useState({});
    const [imageModalOpen,setImageModal]= useState(false);
    const [loading,setLoading] = useState(0);
    const [currentEducation,setCurrentEducation] = useState('NEET PG');
    const [courseName,setcourseName] = useState('');
    const dispatch = useDispatch();

    const handleSubmit = async (value)=>{
        const finalValue = {
            ...value,
            courseId: getCourseIdByName(value.education)
        }
        console.log('finalValue1', finalValue) ; 

        setLoading(true);
        try {
            const res =  await axiosRequestSilent("POST","/student","/updateProfile",finalValue,{});
            if(res.data?.gatewayResponse?.status?.isSuccess){
                toast.success("Updated successfully",{hideProgressBar: true})
                dispatch(syncProfile())
            }else{
                toast.error(res.data?.gatewayResponse?.status?.message,{hideProgressBar: true});
            }
        } catch (error) {
            toast.error("Something went wrong");
        }finally{
            setLoading(false);
        }
    }
    

    function handlerEducation(e){
        console.log(' e.value',  e.value )
         setCurrentEducation( e.value); 
    }

    const deleteCurrentProfile = async()=>{
        try {
            const deleted = await deleteAccount();
            if(deleted){
                await props.Logout();
            }
        } catch (error) {
            console.log(error);
        }
    }

    useEffect(()=>{
        if(!!profileInfo){
            console.log('profileInfo 121', profileInfo )
            const {name , email, education , state, college, studentId,courseId} = profileInfo;
            setInitialState({
                name,email,education,
                state,college,studentId,
                courseId ,
                emailUnSubscribe:false
            }) 

           const courseName = getCourseNameById(courseId);
            setcourseName(courseName);
            setCurrentEducation(education)
        }
    },[profileInfo]);

    const [currentCollegeList,setCurrentCollegeList] = useState([]);

    const decorator = createDecorator(
        {
          field: 'state', // when the value of state changes...
          updates: {
            collegeq: (state, allValues) => {
                console.log(state,allValues);
                setCurrentCollegeList(stateCollegeMap[state])
                if(!!allValues.college){
                    return null;
                }else{
                    return initialState.college4
                }
                
            }
          }
        })

    return (<>
        <div className='row mt-5'>
            <div className='col-lg-8 col-xl-8 col-md-12 col-sm-12 col-12'>
                    <Form onSubmit={handleSubmit} decorators={[decorator]} initialValues={initialState} render={({ handleSubmit }) => (<>
                        {(!(profileInfo?.emailVerified) && !requestSend) && <p className='m-0'>Your email is not verified <span className='text-primary pointer' onClick={async()=>{ await resendEmailVerificationLink();setRequestSend(true)}}><b>click here</b></span> to resend email verification.</p>}
                        <form onSubmit={handleSubmit}  className='mt-2 row justify-content-center'>
                            <Field name='name' className="mb-3"  validate={V.compose(V.required,V.name)}>
                                {({ input, meta }) => (
                                    <Input {...input} {...meta} type="text" className="col-12 col-md-7 mx-auto mt-3" placeholder="Full Name" maxLength={50} />
                                )}
                            </Field>
                            <Field name='email' validate={V.compose(V.email,V.required)}>
                                {({ input, meta }) => (
                                    <Input {...input} {...meta} type="text" className="col-12 col-md-7 mt-3 mx-auto" placeholder="Email Id" maxLength={50} />
                                )}
                            </Field>
                            <Field name='education' validate={V.compose(V.required)}>
                                {({ input, meta }) => (
                                    <Input {...input} {...meta} onchange={handlerEducation({...input})} type="select" 
                                    className="col-12 col-md-7 mx-auto mt-3" placeholder="College">
                                        <option value={""}>Select Education</option> 
                                        {courseList.map((course)=><option value={course} 
                                            >{course}</option>)}
                                    </Input>
                                )}
                            </Field>
                            {/* <Field name='state' validate={V.compose(V.required)}>
                                {({ input, meta }) => (
                                    <Input {...input} onClick={()=>console.log({...meta})} {...meta} type="select" 
                                    className="col-12 col-md-7 mx-auto mt-3" placeholder="State / Country">
                                        <option value={""}>Select state</option>
                                        {stateList.map((state)=><option value={state}>{state}</option>)}
                                    </Input>
                                )}
                            </Field> */}
                            <Field name='courseId' validate={V.compose(V.required)}>
                                {({ input, meta }) => (
                                    <Input {...input} {...meta} type="select" className="col-12 col-md-6 mb-4 mt-3" placeholder="Select Course" >
                                        {/* <option value={''} >Select Year</option> */}
                                        {yearCourseMap(currentEducation).map((education)=><option value={education}>
                                            {education} 
                                        </option>)}
                                    </Input>
                                )}
                            </Field>
                            <Field name='college' validate={V.compose(V.required,V.collge)}>
                                {({ input, meta }) => (
                                    <Input {...input} {...meta} {...{touched:true,visited:true}} type="select" className="col-12 col-md-7 mx-auto mt-3" placeholder="College">
                                        <option value={""}>Select College</option>
                                        {!!currentCollegeList && currentCollegeList.map((college)=><option value={college}>{college}</option>)}
                                    </Input>
                                )}
                            </Field>
                            <Field name="emailUnSubscribe" type='checkbox'>
                                {({input,meta})=>(
                                    <div className='row align-items-center justify-content-between mt-3' style={{padding: '0px 0px', margin: '5px 0px'}}>
                                    <div className='col-6 p-0'>
                                        <div className="mb-3 form-check">
                                            <input {...input} {...meta} type="checkbox" className="form-check-input" id="exampleCheck1"></input>
                                            <label className="form-check-label" for="exampleCheck1">Unsubscribe to Emails</label>
                                        </div>
                                    </div>
                                    <div className='col-6 text-end p-0'>
                                        <button type='submit' disabled={loading} className='btn btn-primary roundedcorner'>Save</button>
                                    </div>
                                </div>
                                )}
                                
                            </Field>
                        </form>
                        <p className='text-center sm-font text-danger pointer'> <span onClick={deleteCurrentProfile}><AiTwotoneWarning /> &nbsp; Delete Account</span></p>
                    </>)} />
            </div>
            <div className='col d-flex justify-content-center'>
                <div>
                    <div className='mt-5'>
                        <div className='border border-secondary rounded p-2'><img src={manicon} width={"80"} /></div>
                        <div onClick={()=> setImageModal(true)} className='border border-secondary rounded p-1 pointer mt-2' style={{width: "80", textAlign: "center"}}><img src={camera} width={"20"}/>&nbsp; Add Image</div>
                    </div>
                </div>
            </div>
            <UploadImageModal isOpen={imageModalOpen} closeModal={()=>setImageModal(false)} />
        </div>
        </>
    )
}

const mapStateToProps= (state)=>({
    authState: state.authReducer
});

const mapDispatchToProps = (dispatch)=>({
    Logout: ()=>dispatch(logout())
})

export default connect(mapStateToProps,mapDispatchToProps)(Profile);
