import { useState } from 'react'
import  Password  from './Password'
import PhoneNumber  from './PhoneNumber'
import  Profile  from './Profile'
import { Reset } from './Reset'
import {withRouter} from 'react-router';

const tabs = ['Profile', 'Change Password', 'Change Phone Number', 'Reset']

export const Main = () => {
    const [selectedTab, setSelectedTab] = useState(0);
    const [requestSend,setRequestSend] = useState(false);

    return (
        <>
            <div className="row mb-2 align-itmes-center">
                <div className="col-12">
                    <h2>Profile</h2>
                </div>
                <div className='col-12'>
                    <ul className='profile-nav'>
                        {tabs.map((item, i) => (<li key={i}><a role='button' className={selectedTab === i ? 'active' : ''} onClick={() => setSelectedTab(i)}>{item}</a></li>))}
                    </ul>
                </div>
            </div>
            {selectedTab === 0 ? <Profile requestSend={requestSend} setRequestSend={setRequestSend} /> : selectedTab === 1 ? <Password /> : selectedTab === 2 ? <PhoneNumber /> : <Reset />}
        </>
    )
}
export default withRouter(Main);