import { useEffect, useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import {withRouter} from 'react-router';
import { Dropdown, DropdownMenu } from 'reactstrap'
import {connect } from 'react-redux'
import logo from 'assets/Logo.png'
import instagram from 'assets/instagram.png'
import facebook from 'assets/facebook.png'
import youtube from 'assets/Mask Group 7.png'
import telegram from 'assets/telegram (1).png'
import { logout } from 'store/authentication/action';
import ChangeCourseModal from './ChangeCourseModal';

export const Header = (props) => {

    const history = useHistory()
    const [isDropdownOpen, setDropdownOpen] = useState(false)
    const [isSidebarOpen, setSidebarOpen] = useState(false)
    const [courseModal,setCourseModal] = useState(false);
    const {Logout , authState} = props;


    useEffect(() => {
        setSidebarOpen(false)
        setDropdownOpen(false)
    }, [history.location.pathname, history.location.search]);

    
    useEffect(()=>{
        if(authState.isAuthenticated){
        console.log('qstnState islogin  useEffect', authState.profileInfo ); 
        }else{
            console.log('qstnState useEffect not ', authState); 
        }
    },[authState.isAuthenticated]);

    const toggleDropdown = () => setDropdownOpen(!isDropdownOpen)
    const toggleSidebar = () => setSidebarOpen(!isSidebarOpen)

    const handleLinkClick = (id)=>{
        const element = document.getElementById(id);
        if(!!element) element.scrollIntoView({ behavior: 'smooth'});
    }
    const navigateToURL = (url)=>{
        window.open(url)
    }

    const isDashboardOpen = history.location.pathname.startsWith('/dashboard')
    console.log('isDashboardOpen s', isDashboardOpen) ; 
    if (isDashboardOpen) return (
        <div className='container-fluid shadow py-3 px-5 header-component'>
            <div className="row align-items-end">
                <div className="col-2 text-center text-md-left">
                    <img src={logo} height='100' className='pointer' alt='logo' onClick={()=>window.location.reload()} />
                </div>
                <div className='col d-flex my-auto'>
                    <div className='d-flex position-relative hover-active'>
                        <Link to='/dashboard' onClick={()=>setCourseModal(true)} className='btn btn-blue change-course-button'>Change Course</Link>
                        {/* <div className='d-none card child-change-course hover-active'>
                            <div>NEET PG</div>
                            <div>FMGE</div>
                            <div>NEET SS</div>
                            <div>MBBS CURRICULUM</div>
                        </div> */}
                    </div>
                </div>
                <div className='col-auto mb-md-5 d-none d-md-flex align-items-center'>
                    <div className='me-4'>
                        <img className='pointer' onClick={()=>window.open("https://www.instagram.com/doctutorials_","_blank")} src={instagram} height='25' alt='instagram' />
                    </div>
                    <div className='me-4'>
                        <img className='pointer' onClick={()=>window.open("https://www.facebook.com/doctutorials","_blank")} src={facebook} height='32' alt='facebook' />
                    </div>
                    <div className='me-4'>
                        <img className='pointer' onClick={()=>window.open("https://www.youtube.com/doctutorials","_blank")} src={youtube} height='25' alt='youtube' />
                    </div>
                    <div className='me-5'>
                        <img className='pointer' onClick={()=>window.open("https://t.me/DocTutorials","_blank")} src={telegram} height='25' alt='telegram' />
                    </div>
                    <div className='me-5'>
                        <p className='text-primary pointer mb-0' onClick={Logout}><b>Logout</b></p>
                    </div>
                    <div className='ms-5 user-name'>
                    <div className='mx-3'>
                    <Link to={"/dashboard/profile"}>
                        <img src={facebook} /> 
                           {props?.authState?.profileInfo?.name}
                            </Link>
                            </div>
                        </div>
                    </div>
            </div>
            {courseModal && <ChangeCourseModal isOpen={courseModal} closeModal={()=> setCourseModal(false)}/>}
        </div>
    )
    return (
        <div className='container pt-md-4 header-component'>
            <div className={`sidebar-nav-container p-3 ${isSidebarOpen ? 'active' : ''} d-md-none`}>
                <span className={`fa fa-${isSidebarOpen? 'times' : 'bars'} fa-2x mt-4 ms-3`} role='button' onClick={toggleSidebar}></span>
                
                <div className={`font-18 ${isSidebarOpen ? 'd-block' :'d-none'}`}>
                    <div className='mt-5'>
                        <Link to='/about' className='nav-link'>About</Link>
                    </div>
                    <div className='mt-3'>
                        <Dropdown isOpen={isDropdownOpen} toggle={toggleDropdown}>
                            <a className='nav-link' onClick={toggleDropdown}>Courses <i className={`fa fa-caret-${isDropdownOpen ? 'up' : 'down'}`}></i></a>
                            <DropdownMenu className='mt-5 rounded-3'>
                                {['NEET PG', 'FMGE', 'NEET SS', 'MBBS CURRICULUM', 'PG RESIDENCY'].map((item, i) => (
                                    <div key={i}>
                                        <Link to={`/course/${item.toString()}`} className='nav-link font-12 font-medium'>{item}</Link>
                                    </div>
                                ))}
                            </DropdownMenu>
                        </Dropdown>
                    </div>
                    <div className='mt-3'>
                        <Link to='/reviews' className='nav-link'>Reviews</Link>
                    </div>
                    <div className='mt-3'>
                        <Link to='/plans' className='nav-link'>Plans</Link>
                    </div>
                    <div className='mt-3'>
                        <Link to='/reach-us' className='nav-link'>Reach Us</Link>
                    </div>
                    <div className='my-5 text-center'>
                        <Link to='/?login=true' className='btn btn-blue'>Login/signup</Link>
                    </div>
                </div>
            </div>
            <div className="row align-items-end pt-3">
                <div className="col text-center text-md-left">
                    <img src={logo} height='100' alt='logo' />
                </div>
                <div className='col-auto mb-md-4 d-none d-md-flex align-items-center'>
                    <div className='me-4'>
                        <img className='pointer' onClick={()=>window.open("https://www.instagram.com/doctutorials_","_blank")} src={instagram} height='25' alt='instagram' />
                    </div>
                    <div className='me-4'>
                        <img className='pointer' onClick={()=>window.open("https://www.facebook.com/doctutorials","_blank")} src={facebook} height='32' alt='facebook' />
                    </div>
                    <div className='me-4'>
                        <img className='pointer' onClick={()=>window.open("https://www.youtube.com/doctutorials","_blank")} src={youtube} height='25' alt='youtube' />
                    </div>
                    <div className='me-4'>
                        <img className='pointer' onClick={()=>window.open("https://t.me/DocTutorials","_blank")} src={telegram} height='25' alt='telegram' />
                    </div>
                    <Link to='/?login=true' className='btn btn-blue'>Login/signup</Link>
                </div>
            </div>
            <div className='row d-none d-md-flex'>
                <div className='col-auto ms-auto'>
                    <span  onClick={()=>handleLinkClick("about")} className='nav-link pointer'>About</span>
                </div>
                <div className='col-auto ms-4'>
                    <Dropdown isOpen={isDropdownOpen} toggle={toggleDropdown}>
                        <a className='nav-link' onClick={toggleDropdown}>Courses <i className={`fa fa-caret-${isDropdownOpen ? 'up' : 'down'}`}></i></a>
                        <DropdownMenu className='mt-5 rounded-3'>
                            {['NEET PG', 'FMGE', 'NEET SS', 'MBBS CURRICULUM', 'PG RESIDENCY'].map((item, i) => (
                                <div key={i}>
                                    <Link to={`/course/${item.toString()}`} className='nav-link font-12 font-medium'>{item}</Link>
                                </div>
                            ))}
                        </DropdownMenu>
                    </Dropdown>
                </div>
                <div className='col-auto ms-4'>
                    <span  onClick={()=>handleLinkClick("review")} className='nav-link pointer'>Reviews</span>
                </div>
                <div className='col-auto ms-4'>
                    <Link href='/plans' className='nav-link pointer'>Plans</Link>
                </div>
                <div className='col-auto ms-4'>
                    <span  onClick={()=>handleLinkClick("reach-us")} className='nav-link pointer'>Reach Us</span>
                </div>
                
            </div>
            
        </div>
    )
}
const mapStateToProps = (state) => ({
    authState: state.authReducer
  })
  const mapDispatchToProps = (dispatch)=>({
    Logout: ()=>dispatch(logout())
  })
  export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Header));
