import image1 from 'assets/Robot operating on patient.png'
import playButton from 'assets/Play.png'
import QRCode from './../assets/QRCode.png'
import planImage from 'assets/card-vector-01.png'
import preparationImage from 'assets/card-vector-02.png'
import mcqCore from 'assets/57691.png'
import learnImage from 'assets/Group 1052.png'
import understandImage from 'assets/Group 1112.png'
import achieveImage from 'assets/Group 1760.png'
import notesImage from 'assets/Notes.png'
import { GetInTouch } from 'components/GetInTouchForn'
import { Plans } from 'components/Plans'
import { DocTutorialCommunity } from 'components/DocTutorialCommunity'
import { useEffect } from 'react'
import { connect } from 'react-redux';
import {loadGeneral,loadAuthenticated} from './../store/home/action';
import { useState } from 'react'
import VideoModal from 'components/VideoModal'



export const Home = (props) => {
    
    const {homeData} = props.homeState;
    const {authState,loadGeneralCall, loadAuthenticatedCall } = props;
    const [showModal,setShowModal] = useState(false);

    useEffect(()=>{
        if(authState.isAuthenticated){
            loadAuthenticatedCall();
        }else{
            loadGeneralCall();
        }
    },[authState.isAuthenticated]);

    return (
    <div className='home'>
        <div className='row mt-md-5'>
            <div className='col-12 col-md-5'>
                <div className='text-primary font-light text-center text-md-left'><h5>Comprehensive Coaching Platform for</h5></div>
                <div className='mt-2 text-center text-md-left'>
                    <h3><b>NEET PG | FMGE | NEET SS |</b></h3>
                    <h5><b>MBBS CURRICULUM | PG RESIDENCY</b></h5>
                </div>
                <div className='mt-md-5'>
                    DocTutorials offers comprehensive online coaching for NEET PG, FMGE and NEET SS through well-structured, high quality audio-visual medical content.<br /><br />
                    Top medical faculty, specialists and super specialists have joined hands with DocTutorials to develop clinically enriched learning and revision programs for NEET PG, FMGE and NEET SS online preparation.
                </div>
                <div className='mt-4'>
                    <div>
                        <div className='d-flex align-items-center justify-content-center justify-content-md-start'>Download DocTutorials <h2 className='mx-2'><b>App</b></h2> Here</div>
                        <div className='ml-33'><img src={QRCode} /></div>
                    </div>
                </div>
                
            </div>
            <div className='col-12 col-md-7 mt-5'>
                <div><img src={image1} className='img-fluid' alt='' /></div>
                <div className='mt-5 demo-video-btn' onClick={()=>setShowModal(true)}>
                    <img src={playButton} className='mx-auto' alt='' height={90} />
                <div className='demo-video-btn-text'>DEMO VIDEO</div>
                </div>
            </div>
        </div>

        <div className='mt-5 mt-md-10'>
            <div className='text-center text-md-left'>
                <h3>Why</h3>
                <h1 className='font-medium'>DocTutorials</h1>
            </div>
            <div className='row mt-4'>
                <div className='col-12 col-md-3 mb-5'>
                    <div className='card shadow text-center p-5'>
                        <h2>Your Plans</h2>
                        <h2>Your Choice!</h2>
                        <div className='image-box box-1 mt-5'>
                            <img src={planImage} alt='' />
                        </div>
                    </div>
                </div>
                <div className='col-12 col-md-3 mb-5 m-auto'>
                    <div className='card shadow text-center p-5'>
                        <h2>More Power to</h2>
                        <h2>your Preparation!</h2>
                        <div className='image-box box-1 mt-5'>
                            <img src={preparationImage} alt='' />
                        </div>
                    </div>
                </div>
                <div className='col-12 col-md-3 mb-5'>
                    <div className='card shadow text-center p-5'>
                        <h2>Access the</h2>
                        <h2>MCQ core</h2>
                        <div className='image-box box-1 mt-5'>
                            <img src={mcqCore} alt='' />
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div className='mt-5 mt-md-10' id="about">
            <h3>Your journey with</h3>
            <h1 className='font-medium'>DocTutorials</h1>

            <div className='row mt-4'>
                <div className='col-12 col-md-5 m-auto mb-5'>
                    <div className='card shadow p-3 bg-light-blue'>
                        <h3><b>Learn</b></h3>
                        <div className='image-box box-1'>
                            <img src={learnImage} alt='' />
                        </div>
                        <div className='m-3'>Learn clinical concepts from our Multi-Disciplinary Approach (MDA) videos and lectures. Study from well-structured soft copy notes, MCQs, IBQs and VBQs on our app for your NEET PG/NEXT preparation, FMGE preparation and NEET SS preparation.</div>
                    </div>
                </div>
                <div className='col-12 col-md-5 m-auto mb-5'>
                    <div className='card shadow p-3 bg-light-gray'>
                        <h3><b>Understand</b></h3>
                        <div className='image-box box-1'>
                            <img src={understandImage} alt='' />
                        </div>
                        <div className='m-3'>Understand important aspects of your exam curriculum through our clinical case scenarios-based questions. Clear your doubts with top faculty for NEET PG/NEXT online coaching, FMGE online coaching and NEET SS online coaching.</div>
                    </div>
                </div>
                <div className='col-12 col-md-5 m-auto mb-5'>
                    <div className='card shadow p-3 bg-light-gray'>
                        <h3><b>Achieve</b></h3>
                        <div className='image-box box-1'>
                            <img src={achieveImage} alt='' />
                        </div>
                        <div className='m-3'>Achieve success! Use our app calendar to schedule your NEET PG/NEXT preparation, FMGE preparation and NEET SS preparation. Practice regularly with revision tests, live tests and grand tests to improve your All India Rank and score.</div>
                    </div>
                </div>
                <div className='col-12 col-md-5 m-auto mb-5'>
                    <div className='card shadow p-3 bg-light-blue'>
                        <h3><b>Revise</b></h3>
                        <div className='image-box box-1'>
                            <img src={planImage} alt='' />
                        </div>
                        <div className='m-3'>Revise high yield topics and previous years questions in all subjects with our test and discussion videos and quick revision programs. Use bookmarked questions with explanations for NEET PG/NEXT preparation, FMGE preparation and NEET SS preparation.</div>
                    </div>
                </div>
            </div>
        </div>

        {!!homeData && homeData.coursePlans && <div className='mt-5 mt-md-10'>
            <h1 className='mb-5 font-medium text-center text-md-left'>Courses</h1>
            <div className='row mt-5'>
                <div className='col-12 col-md-auto'>
                    <div className='course-name'>NEET PG</div>
                </div>
                <div className='col ms-md-4 mt-3 align-self-center'>
                    DocTutorials NEET PG/NEXT online coaching app prepares you for one of the most competitive online medical entrance exams. Clear your doubts during our interactive live sessions with top faculty in all 19 subjects. Learn clinical concepts with our clinical videos, multidisciplinary approach (MDA) videos and clinical case scenarios-based questions.
                </div>
            </div>
            <div className='row mt-5'>
                <div className='col-12 col-md-auto'>
                    <div className='course-name'>FMGE</div>
                </div>
                <div className='col ms-md-4 mt-3 align-self-center'>
                    DocTutorials prepares medical graduates from overseas for their FMGE screening test. Our FMGE online coaching app offers systematic guidance for MBBS pre-clinical, para-clinical and clinical subjects. Our top faculty covers the necessary topics and clears your doubts from the MBBS syllabus required for your FMGE preparation.
                </div>
            </div>
            <div className='row mt-5'>
                <div className='col-12 col-md-auto'>
                    <div className='course-name'>NEET SS</div>
                </div>
                <div className='col ms-md-4 mt-3 align-self-center'>
                    DocTutorials NEET SS online coaching app prepares you for one of the most competitive online medical entrance exams for super specialty.<br/> <br />
                    Interactive live sessions and video lectures by top specialists and superspecialists, NEET SS Qbank, grand tests and specialty tests with clinical case scenario-based questions help maximize your NEET SS preparation for Part A and Part B.
                </div>
            </div>
            <div className='row mt-5'>
                <div className='col-12 col-md-auto'>
                    <div className='course-name'>MBBS CURRICULUM</div>
                </div>
                <div className='col ms-md-4 mt-3 align-self-center'>
                    DocTutorials NEET SS online coaching app prepares you for one of the most competitive online medical entrance exams for super specialty.<br/> <br />
                    Interactive live sessions and video lectures by top specialists and superspecialists, NEET SS Qbank, grand tests and specialty tests with clinical case scenario-based questions help maximize your NEET SS preparation for Part A and Part B.
                </div>
            </div>
            <div className='row mt-5'>
                <div className='col-12 col-md-auto'>
                    <div className='course-name'>PG RESEDENCY</div>
                </div>
                <div className='col ms-md-4 mt-3 align-self-center'>
                    DocTutorials NEET SS online coaching app prepares you for one of the most competitive online medical entrance exams for super specialty.<br/> <br />
                    Interactive live sessions and video lectures by top specialists and superspecialists, NEET SS Qbank, grand tests and specialty tests with clinical case scenario-based questions help maximize your NEET SS preparation for Part A and Part B.
                </div>
            </div>
        </div>}

        <div className='mt-5 mt-md-10'>
            <h1 className='mb-5 text-center font-medium'>Get Your All Notes</h1>
            <div className='row'>
                <div className='col-12 col-md-6'>
                    <div className='px-5 notes-image'>
                        <div className=''>
                            <img src={notesImage} className='img-fluid' alt='' />
                        </div>
                    </div>
                </div>
                <div className='col-12 col-md-6 h-100'>
                    <div className='mt-3 mt-md-0'>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s... Read more Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s... Read more</div>
                    <div className='text-center text-md-right ms-auto mt-4'>
                        <button className='btn btn-blue large'>BUY</button>
                    </div>
                </div>
            </div>
        </div>

        {!!homeData && homeData.analytics && <div className='mt-5'>
            <h1 className='font-medium text-center text-md-left'>Analytics</h1>
            <div className='row'>
                <div className='col-6 col-md-3 mb-3 mb-md-5 p-md-5'>
                    <div className='card shadow px-5 py-4 p-md-5' >
                        <div className='d-flex align-items-end text-blue'>
                            <h1 className='large-font'><b>{homeData.analytics.appDownlaods}</b></h1>
                            <h3><b>M+</b></h3>
                        </div>
                        <h1 className='text-gray'><b>App</b></h1>
                        <div className='text-gray font-12'>Downloads</div>
                    </div>
                </div>
                <div className='col-6 col-md-3 mb-3 mb-md-5 p-md-5'>
                    <div className='card shadow px-5 py-4 p-md-5' >
                        <div className='d-flex align-items-end text-blue'>
                            <h1 className='large-font'><b>{homeData.analytics.videoHours}</b></h1>
                        </div>
                        <h1 className='text-gray'><b>Hours</b></h1>
                        <div className='text-gray font-12'>Video Watched</div>
                    </div>
                </div>
                <div className='col-6 col-md-3 mb-3 mb-md-5 p-md-5'>
                    <div className='card shadow px-5 py-4 p-md-5' >
                        <div className='d-flex align-items-end text-blue'>
                            <h1 className='large-font'><b>{homeData.analytics.mcqSolved}</b></h1>
                            <h3><b>M+</b></h3>
                        </div>
                        <h1 className='text-gray'><b>MCQ</b></h1>
                        <div className='text-gray font-12'>Solved</div>
                    </div>
                </div>
                <div className='col-6 col-md-3 mb-3 mb-md-5 p-md-5'>
                    <div className='card shadow px-5 py-4 p-md-5' >
                        <div className='d-flex align-items-end text-blue'>
                            <h1 className='large-font'><b>{homeData.analytics.testTaken}</b></h1>
                            <h3><b>M+</b></h3>
                        </div>
                        <h1 className='text-gray'><b>Tests</b></h1>
                        <div className='text-gray font-12'>Taken</div>
                    </div>
                </div>
            </div>
        </div>}
        {showModal && <VideoModal isOpen={showModal} closeModal={()=>setShowModal(false)} />}
        {!!homeData && homeData.testimonials && <DocTutorialCommunity testimonials={homeData.testimonials} />}
        {!!homeData && homeData.testimonials && <Plans plans={homeData.coursePlans} />}
        <GetInTouch />
    </div>)
} 

const mapStateToProps = (state)=>({
    authState: state.authReducer,
    homeState: state.homeReducer
})

const mapDispathToProps = (dispatch)=>({
    loadGeneralCall : ()=> dispatch(loadGeneral()),
    loadAuthenticatedCall: ()=> dispatch(loadAuthenticated())
})
export default connect(mapStateToProps,mapDispathToProps)(Home);