import gridicon from 'assets/Grid View.png';
import listicon from 'assets/list.png';
import playbtn from 'assets/play-button.png';
import { useEffect } from 'react';
import { useState } from 'react';
import { connect } from 'react-redux';
import {getVideos} from './../../store/dashbaord/video/action';
import ReactPlayer from 'react-player'
import {AiFillCloseCircle, AiFillPlayCircle} from 'react-icons/ai';

export const Videos = (props) => {
    const [gridView, setGridView] = useState(false);
    const toggleView = () => setGridView(!gridView);
    const [currentVideo,setCurrentVideo] = useState(null);

    const {fetchVideos,authState,videoState } = props;

    const fetchVideoList = ()=>{
        const body={
            courseId: authState.profileInfo.courseId,
            studentId: authState.profileInfo.studentId
        }
          fetchVideos(body);
    }

    useEffect(()=>{
        console.log(authState);
        if(authState.isAuthenticated){
            fetchVideoList();
        console.log('videoState 1  ', videoState); 

        }
        console.log('videoState ', videoState); 
        setGridView(true); 

    },[]);

    if(!!currentVideo){
        return (<div className='relative'>
                    <div className='top-right'>
                        <AiFillCloseCircle style={{cursor: 'pointer'}} onClick={()=>setCurrentVideo(null)} />
                    </div>
                    <ReactPlayer
                        url={currentVideo?.path}
                        controls={true}
                        config={{ file: { attributes: { controlsList: 'nodownload' } } }}
                        onContextMenu={e => e.preventDefault()}
                        width={"100%"}
                    />
                    <div>
                        <h2>{currentVideo.videoName} by {currentVideo.factultyName}</h2>
                    </div>
                </div>)
    }


    return (
        <>
            <div className={gridView && false ? 'gridView-videos' : 'listView-videos'}>
                <div className="row mb-2 align-itmes-center">
                    <div className="col-lg-6 col-xl-6 col-md-6 col-sm-6 col-6">
                        <h2>Videos</h2>
                    </div>
                    <div className="col-lg-6 col-xl-6 col-md-6 col-sm-6 col-6 text-end">
                        <a onClick={toggleView} id='listiconBtn'><img src={gridView ? listicon : gridicon} /></a>
                    </div>
                </div>
                <div className="row">
                {gridView === true ? (
                    !!videoState?.videosInfo && videoState.videosInfo?.videos && videoState.videosInfo.videos.map((video, i )=>(
                        <div className="col-lg-3 col-xl-3 col-sm-3 col-md-3 col-12 m-2" key={i.toString() } onClick={()=>setCurrentVideo(video)}>
                            <div className="video-card relative">
                                <div className="header">
                                    <p>{video.subjectName}</p>
                                </div>
                                <div className='center opacity-50 f-40 pointer'><AiFillPlayCircle/></div>
                                <div className="vthumbnail border border-primary rounded">
                                    <img src={video.imageURL} width="180" />
                                </div>
                                <div className="cardfooter">
                                    <p><small>{video.videoName}</small></p>
                                    <p>By {video.factultyName}</p>
                                </div>
                            </div>
                    </div>
                    ))
                ) : (
                      <div className='video-card p-2'>
                        <div className='row align-items-center'>
                            <div className='col-auto'>
                            No Data Found 
                            </div>
                        </div>
                        </div>
                     
                )}
                </div>
            </div>
        </>
    )
}

const mapSateToProps = (state)=>({
    authState: state.authReducer,
    videoState: state.dashBoardVideoReducer
})
const mapDispatchToProps = (dispatch)=>({
    fetchVideos: (body)=>dispatch(getVideos(body))
})
export default connect(mapSateToProps,mapDispatchToProps)(Videos);

