import React,{useEffect, useState} from 'react';
import { Button, Modal, ModalBody } from "reactstrap"
import { uploadProfileImage } from 'store/dashbaord/profile/action';
import manicon from './../assets/Group 9547.png';

const UploadImageModal = (props) => {

    const [imageState,setImageState] = useState({name: "", data: "",url:null});
    const [loading,setLoading] = useState(false);
    const [cameraRunning,setCameraRunning] = useState(false);
    const {isOpen,closeModal} = props;
    const placeholder = "https://cdn.pixabay.com/photo/2016/08/08/09/17/avatar-1577909_1280.png"

    const constrains = {
        video: {
          width: {
            min: 200,
            ideal: 250,
            max: 400,
          },
          height: {
            min: 200,
            ideal: 250,
            max: 400
          },
        },
        audio: false 
      }

    const handleClickCapture = ()=> {
        setCameraRunning(false);
        let canvas = document.querySelector("#canvas");
        let video = document.querySelector("#video");
        canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
        let image_data_url = canvas.toDataURL('image/jpeg');
        // data url of the image
        console.log(image_data_url);
        // A video's MediaStream object is available through its srcObject attribute
        const mediaStream = video.srcObject;

        // Through the MediaStream, you can get the MediaStreamTracks with getTracks():
        const tracks = mediaStream.getTracks();

        // Tracks are returned as an array, so if you know you only have one, you can stop it with: 
        tracks[0].stop();
    }

    const handleClickStartCamera = async ()=> {
        setCameraRunning(true);
        let video = document.querySelector("#video");
        let stream = await navigator.mediaDevices.getUserMedia(constrains);
        console.log(stream);
        video.srcObject = stream
        video.play();
        
        
    }



    const handleChange = (e)=>{
        // Get a reference to the file
        const file = e.target.files[0];
        const ext = file.name.substring(file.name.lastIndexOf("."));
        const newName = Date.now() + ext;
        const url = URL.createObjectURL(file);
        // Encode the file using the FileReader API
        const reader = new FileReader();
        reader.onloadend = () => {
            const base64 = reader.result.replace('data:', '')
            .replace(/^.+,/, '');
            setImageState({
                imageName: newName,
                imageLinkBase64:  base64,
                url: url
            });
        };
        reader.readAsDataURL(file);
    }

    const handleSubmit = async ()=>{
        setLoading(true);
        try {
            await uploadProfileImage(imageState);
            setImageState({
                imageLinkBase64: "",
                imageName: ""
            })
            document.getElementById("profileImageUpload").value=null;
        } catch (error) {
            console.log(error);
        }finally{
            setLoading(false);
        }
    }

    const handleBrowse = ()=>{
        const input = document.getElementById("profileImageUpload");
        input.click();
    }

    
  return (
    <div>
        <Modal isOpen={isOpen} centered size="lg" toggle={closeModal}>
            
                <div className='max-w-400'>
                    <div>
                        <input id={"profileImageUpload"} type={"file"} hidden accept={"image/*"}  multiple={false} onChange={handleChange} />
                    </div>
                    {true && <div className='d-flex justify-content-center'>
                        <div className='d-flex justify-content-center dashed-border border border-secondary rounded w-200'>
                            <img className='border border-secondary rounded-circle m-4' height={"80"} width={"80"} src={imageState.url || manicon} />
                        </div>
                    </div>}
                    {false && <div className='d-flex justify-content-center'>
                    <video className={cameraRunning?'border border-dark center d-block':'border border-dark center d-none'}  width="200" height="200" id="video"></video>
                    </div>}
                    <div className='d-flex justify-content-center'>
                        <button className='btn btn-outline-secondary my-2 mx-2' onClick={handleBrowse}>Browse</button>
                        <button onClick={handleClickStartCamera} className='btn btn-outline-secondary my-2' id="start-camera">Camera</button>
                    </div>
                    <div className='d-flex justify-content-center'>
                        <button onClick={handleClickStartCamera} className='btn btn-outline-secondary my-2' id="start-camera">Upload</button>
                    </div>
                </div>
                {/* <hr></hr>
                <div>
                    <div className='d-flex justify-content-between align-items-center'>
                        <div className='relative two-00'>
                            <video className={cameraRunning?'border border-dark center d-block':'border border-dark center d-none'}  width="200" height="200" id="video"></video>
                            <canvas id="canvas" className={cameraRunning?'border border-dark center d-none':'border border-dark center d-block'} width="200" height="200"></canvas>
                        </div>
                        <div>
                            {!cameraRunning ? 
                            <Button onClick={handleClickStartCamera} className='btn-primary' id="start-camera">Camera</Button>
                            :
                            <Button onClick={handleClickCapture} className='btn-primary' id="click-photo">Click Photo</Button>
                            }
                        </div>
                    </div>
                    <div className='d-flex justify-content-between align-items-center'>
                        <div>&nbsp;</div>
                        <div></div>
                    </div>
                    
                </div> */}
        </Modal>
    </div>
  )
}

export default UploadImageModal