import { Switch, Route, Redirect } from 'react-router-dom'
import  Home from 'pages/Home'
import  {Login}  from 'pages/Login'
import {Signup}  from 'pages/Signup'
import  Course from 'pages/Course'
import  {Dashboard}  from 'pages/Dashboard'
import { Footer } from './footer'
import  Header  from './Header'
import { FAQs } from 'pages/FAQs'
import PrivateRoute from './PrivateRoute'
import Blog from './../pages/blog/Main'
import PublicRoute from './PublicRoute'
import { useEffect } from 'react'
import { useState } from 'react'
import Plan from 'pages/Dashboard/Plan';
import UploadImageModal from './UploadImageModal'


export const Router = ({ history }) => {
    const isDashboardOpen = history?.location?.pathname?.includes('/dashboard')
    const [path,setPath] = useState("");

    useEffect(()=>{
        setPath( history?.location?.pathname)
        console.log(path)
    },[history?.location?.pathname]);
    
    return (<>
        <div className= {path == "/" ? 'bg-main':''}>
            <Header />
            <div className={`${isDashboardOpen ? 'container-fluid' : 'container py-5'}`}>
                <Switch>
                    <PublicRoute exact path='/' component={Home} />
                    <PublicRoute exact path='/plans' component={Plan} />
                    <PublicRoute path='/course/:courseName' component={Course} />
                    <PublicRoute path='/blogs' component={Blog} />
                    <PrivateRoute path='/dashboard' component={Dashboard} />
                    <PublicRoute path='/faqs' component={FAQs} />
                    <PublicRoute path='/upload' component={UploadImageModal} />
                    <Redirect exact to='/' />
                </Switch>
                <Login />
                <Signup />
            </div>
            <Footer />
        </div>
    </>)
}

export default  Router