import { useState } from "react";

export const Reset = () => {
    const [state,setState] = useState();
    return (
        <div className='row mt-5'>
            <div className='max-w-550'>
                <h6 className="text-primary mb-4 bold">You Can Reset Your Activity and Start from Scratch</h6>
                <form>
                    <div className='d-flex align-items-center w-100 mt-0'>
                        <div className="d-flex justify-content-center align-items-center"><input name={"curr"} type={"radio"} /> &nbsp;</div> <div className="text-primary"><b>Question Bank</b></div>
                    </div>
                    <div className='d-flex  w-100'>
                        <div className="pl-3 mb-3">Clear all your question bank activities and erase score</div>
                    </div>  
                    <div className='d-flex align-items-center w-100 mt-0'>
                        <div className="d-flex justify-content-center align-items-center"><input name="curr" type={"radio"} /> &nbsp;</div> <div className="text-primary"><b>Bookmarks</b></div>
                    </div>
                    <div className='d-flex w-100 mb-3'>
                        <div className="pl-3">Clear all your Bookmarked MCQs and start from scratch</div>
                    </div>
                    <div className='text-end'>
                        <button type='submit' className='btn btn-primary border-sharp sm-font'>Reset Activity</button>
                    </div>                             
                    
                </form>
            </div>
        </div>
    )
}

