import moment from 'moment';
import React from 'react';
import { getSubArrayData } from 'utils/methods';


const BlogBody = (props) => {
    const blogs = getSubArrayData(props.blogs,2);
  return (
    <div>
        {!!blogs && blogs.length > 0 && blogs.map((_blog)=>(
        <div className="row" key={Math.random()}>
            {_blog.length > 0 && _blog.map((blog_i)=>(
            <div className="col-md-6 my-2 pointer" key={Math.random()} onClick={()=>{props.setCurrentBlog(blog_i.blogId)}}>
                <div className="card light-border">
                    <img src={blog_i.image} className="card-img-top max-400" alt="NA IMg" />
                    <div className="card-body">
                        <p className="card-text text-primary">{!!blog_i.title?blog_i.title:"Title : NA"}</p>
                        <p className="text-muted text-card-sm">Post : {moment(blog_i.createdAt).format("MMM Do YY")}</p>
                    </div>
                </div>
            </div>
            )) }
            
        </div>
        ))}
    </div>
  )
}

export default BlogBody;