import React, { useEffect, useState } from 'react';
import BlogBody from './component/Body';
import Pagination from './component/Pagination';
import { getBlogInfo } from 'store/blogs/action';
import { connect } from 'react-redux';
import IndividulaBlog from './component/IndividulaBlog';

const Main = (props) => {
  const {blogInfo: {blogInfo},setBlogInfo} = props;
  const [courseId,setCourseId] = useState(1);
  const [currentBlog,setCurrentBlog] = useState(null);

  useEffect(()=>{
    setBlogInfo({courseId});
  },[courseId]);

  return (
    <div>
      {!currentBlog && <div className='d-flex pointer'>
        <div className={courseId == 1 ? 'text-primary': '' + 'mx-2'} onClick={()=>setCourseId(1)}>NEET PG</div>
        <div className={courseId == 3 ? 'text-primary': '' + 'mx-2'} onClick={()=>setCourseId(3)}>NEET SS</div>
        <div className={courseId == 2 ? 'text-primary': '' + 'mx-2'} onClick={()=>setCourseId(2)}>FMGE</div>
        <div className={courseId == 4 ? 'text-primary': '' + 'mx-2'} onClick={()=>setCourseId(4)}>MBBS CURRICULUM</div>
        <div className={courseId == 5 ? 'text-primary': '' + 'mx-2'} onClick={()=>setCourseId(5)}>PG RESIDENCY</div>
      </div>}
        {!!currentBlog && <h5 
          className='text-primary'
          onClick={()=>setCurrentBlog(null)}
          >
            Back To Blogs
          </h5>}
        {!currentBlog && <BlogBody 
          setCurrentBlog={setCurrentBlog}
          blogs={!!blogInfo?.content? blogInfo.content :[]} 
        />}
        {!!currentBlog && <IndividulaBlog blog={currentBlog} />}
        {!currentBlog && <Pagination />}

    </div>
  )
}
const mapStateToProps = (state)=>({
  blogInfo: state.blogReducer
})

const mapDispatchToProps = (dispatch)=>({
  setBlogInfo: (body)=>dispatch(getBlogInfo(body))
})

export default connect(mapStateToProps,mapDispatchToProps)(Main);