import AllBookmark from "./AllBookmark"
import GrandText from "./GrandText"
import { useState } from "react"
import { BsSearch } from 'react-icons/bs'


const tabs = ['All', 'Grand Test']

export const Bookmark = () => {
    const [selectedTab, setSelectedTab] = useState(0)

    return (
        <>
            <div className="row mb-2 align-itmes-center bookmark-section">
                <div className="col-12 mb-5">
                    <h2><b>Bookmarked</b></h2>
                </div>
                <div className='col-6 ms-2'>
                    <ul className='profile-nav'>
                        {tabs.map((item, i) => (<li key={i}><a role='button' className={selectedTab === i ? 'active' : ''} onClick={() => setSelectedTab(i)}>{item}</a></li>))}
                    </ul>
                </div>
                <div className="col-4">
                    <div class="input-group mb-3 search-inputBoxModel" >
                        <input type="text" class="form-control sm-3 input-searchBox-bookmark" placeholder="Search by Subjects..." aria-label="Recipient's username" aria-describedby="basic-addon2" />
                        <div class="input-group-append">
                            <button class="btn btn-primary " type="button" style={{ marginLeft: "-1.1rem" }}>&nbsp;<BsSearch className="icon-search-bookmark" />&nbsp;</button>
                        </div>
                    </div>
                </div>
            </div>

            {selectedTab === 0 ? <AllBookmark /> : <GrandText />}
        </>
    )
}

