import React, { useEffect } from 'react';
import { Route, Redirect, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { tryToAuthenticate } from 'store/authentication/action';

const PrivateRoute = ({ component: Component, authState, tryTologin,...rest }) => {
    const {authenticationInProgress,isAuthenticated} = authState;
    useEffect(async ()=>{
        if(!isAuthenticated && !authenticationInProgress){
            const bool = await tryTologin();
            const prevPath = localStorage.getItem("prevPath");
            if(bool && !!prevPath && prevPath != "/"){
                rest.history.push(prevPath);
            }
        }
        if(rest.location.pathname != "/"){
            localStorage.setItem("prevPath", rest.location.pathname);
        }
    },[isAuthenticated,rest.location.pathname]);

    return (
        <Route
            {...rest}
            render={props=> isAuthenticated ? <Component {...props} /> :<Redirect to="/" />
            }
        />
    )
};
const mapStateToProps = (state) => ({
    authState: state.authReducer,
});
const mapDispatchToProps = (dispatch)=>({
    tryTologin: ()=> dispatch(tryToAuthenticate())
})
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(PrivateRoute));

