import { Button } from "reactstrap";

export const TestSeriesReview = () => {
  return (
    <div className="test-series-review-wrapper">
      <div className="row">
        <div className="col-lg-12">
            <div className="total-score-wrapper">
                <div>You Scored 20%</div>
                <div>20 / 100</div>
            </div>
        </div>
        <div className="col-lg-12 text-center">
          <div className="row justify-content-center">
            <div className="col-lg-4 box">
              <div className="text-center">Total MCQs: 50</div>
              <div className="row h-90 align-items-center">
                <div className="col-lg-7 chart-wrapper"></div>
                <div className="col-lg-5">
                  <div className="wrapper">
                    <span className="correct"></span>
                    Correct
                  </div>
                  <div className="wrapper">
                    <span className="wrong"></span>
                    Wrong
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-3 box">
              <div className="row text-center">
                <div className="col-lg-6 separator bottom">
                  <div className="percent">2%</div>
                  <div>Accuracy</div>
                </div>
                <div className="col-lg-6 separator bottom left">
                  <div className="percent">01:20</div>
                  <div>Total Time</div>
                </div>
                <div className="col-lg-6 separator">
                  <div className="percent">2s/Q</div>
                  <div>Speed</div>
                </div>
                <div className="col-lg-6 separator left">
                  <div className="percent">5s</div>
                  <div>Accuracy</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg-12 text-center btn-wrapper">
        <Button className="save">Review Answers</Button>
        </div>
      </div>
    </div>
  );
};
