import Collapse from 'assets/Collapse.png'
import videoicon from 'assets/video-Black.png'
import qbank from 'assets/Question Bank.png'
import testseriesicon from 'assets/Test Series.png'
import leaderboard from 'assets/Leaderboard.png'
import bookmark from 'assets/bookmark.png'
import custommodule from 'assets/Custom Model.png'
import plans from 'assets/Plans.png'
import { useRouteMatch, Link } from 'react-router-dom'
import { useState } from 'react'
import { useHistory } from 'react-router-dom'


export const Sidebar = () => {
    const { path } = useRouteMatch()
    const history = useHistory()
    const [isOpen, setOpen] = useState(true)
    const toggleSidebar = () => setOpen(!isOpen)

    const pathname = history.location.pathname.replace('/dashboard', '');

    return (
        <nav id="sidebar" className={isOpen ? '' : 'active'}>
            <a id="sidebarCollapse" className={isOpen?"collapsebtn":"collapsebtn invert"} onClick={toggleSidebar} >
                <img src={Collapse} width="32" height="32" />
            </a>
            <ul className="list-unstyled components mt-3">
                <li className={pathname.startsWith('/videos') ? isOpen? 'active-tab': 'active-tab-shrinked' : ''}>
                    <Link to={path + '/videos'}>
                        <img src={videoicon} />
                        {isOpen ? 'Videos': ''}
                    </Link>
                </li>
                <li className={pathname.startsWith('/question-bank') ? isOpen? 'active-tab': 'active-tab-shrinked' : ''}>
                    <Link to={path + '/question-bank'}>
                        <img src={qbank} />
                        {isOpen ? 'Question Bank': ''}
                    </Link>
                </li>
                <li className={pathname.startsWith('/test-series') ? isOpen? 'active-tab': 'active-tab-shrinked' : ''}>
                    <Link to={path + '/test-series'}>
                        <img src={testseriesicon} />
                        {isOpen ? 'Test Series': ''}
                    </Link>
                </li>
                <li className={pathname.startsWith('/leaderboard') ? isOpen? 'active-tab': 'active-tab-shrinked' : ''}>
                    <Link to={path + '/leaderboard'}>
                        <img src={leaderboard} />
                        {isOpen ? 'Leaderboard': ''}
                    </Link>
                </li>
                <li className={pathname.startsWith('/bookmark') ? isOpen? 'active-tab': 'active-tab-shrinked' : ''}>
                    <Link to={path + '/bookmark'}>
                        <img src={bookmark} />
                        {isOpen ? 'Bookmarks': ''}
                    </Link>
                </li>
                <li className={pathname.startsWith('/custom-module') ? isOpen? 'active-tab': 'active-tab-shrinked' : ''}>
                    <Link to={path + '/custom-module'}>
                        <img src={custommodule} />
                        {isOpen ? 'Custom Module': ''}
                    </Link>
                </li>
                <li className={pathname.startsWith('/plans') ? isOpen? 'active-tab': 'active-tab-shrinked' : ''}>
                    <Link to={path + '/plan'}>
                        <img src={plans} />
                        {isOpen ? 'Plans': ''}
                    </Link>
                </li>
            </ul>

            <ul className="list-unstyled CTAs">
                <div className='powerpack'>
                    <strong>PG powerpack</strong><br></br>
                    <small>End Date: 08-07-2022</small>
                </div>
            </ul>
        </nav>
    )
}