import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import { axiosRequestSilentPublic } from 'store/apis/api-v1';

const IndividulaBlog = (props) => {
    const {blog} = props;
    const [individulaBlog,setIndividualBlog] = useState(null);

    const fetchIndividualBlog = async ()=>{
        try {
            const res = await axiosRequestSilentPublic("POST","/dataInfo","/studentBlogDetails",{blogId: blog},{});
            if(res.data.gatewayResponse.status.isSuccess){
                setIndividualBlog(res.data.gatewayResponse.response.content[0]);
                console.log(individulaBlog);
            }else{
                toast.error(res.data.gatewayResponse.status.message);
            }
        } catch (error) {
            toast.error("something went wrong ...");
        }
    }

    useEffect(()=>{
        fetchIndividualBlog();
    },[]);

  return (
    <div>
        {!!individulaBlog && <div>
            <div>
                <div>
                    <img src={individulaBlog.image} className="img-fluid" alt="NA IMg" />
                </div>
                <h2 className="text-primary my-2">{!!individulaBlog.summary?individulaBlog.summary:"Title : NA"}</h2>
                <h4 className="text-muted mb-2">Post : {moment(individulaBlog.createdAt).format("MMM Do YY")}</h4>
                <p>{individulaBlog.content}</p>
            </div>
        </div>}
    </div>
  )
}

export default IndividulaBlog