import React, { useState, useRef } from 'react';
import gridicon from 'assets/Grid View.png';
import listicon from 'assets/list.png';
import { useEffect } from 'react';
import { connect } from 'react-redux';
import {getQB_Anatomy} from './../../store/dashbaord/anatomy/action';
import ReactPlayer from 'react-player'
import {AiFillCloseCircle, AiFillPlayCircle} from 'react-icons/ai';
import { useRouteMatch, Link } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import {
  AccordionBody,
  AccordionItem,
  AccordionHeader,
  UncontrolledAccordion,
  Button,
  Input,
  FormGroup,
  Form,
  UncontrolledTooltip  ,
  Label
} from "reactstrap";



export const Anatomy = (props) => {
  const { path } = useRouteMatch()
  const [gridView, setGridView] = useState(true);
  const [tooltipOpen, settooltipOpen] = useState(true);

  const [show, setShow] = useState(false);
  const target = useRef(null);
  const toggleView = () => setGridView(!gridView);
  const {fetchQB_Anatomys,authState,qstnState } = props;
  const { qbid } = useParams();
  const fetchQB_AnatomyList = ()=>{
      const body={
          courseId: authState.profileInfo.courseId,
          studentId: authState.profileInfo.studentId,
          subjectId: qbid,
          qrp: "false"
      }
      console.log('0 body fetchQB_Anatomys ', body );
      fetchQB_Anatomys(body);
  }

  useEffect(()=>{
      console.log(authState);
      if(authState.isAuthenticated){
        fetchQB_AnatomyList();
      }
  },[authState.isAuthenticated]);

  const handleChange = (e)=>{ 
    let isChecked = e.target.checked;
    console.log('isChecked', isChecked);
    // do whatever you want with isChecked value
  }
  return (
    <div className="anatomy-wrapper">
      <UncontrolledAccordion defaultOpen="1">
      { !!qstnState?.QBInfo &&  qstnState.QBInfo?.topic ? 
         !!qstnState.QBInfo?.topic && qstnState.QBInfo.topic.map((data,i) => (
         <AccordionItem key={i.toString()}>
                <AccordionHeader targetId={(i+1).toString()}>
                  <span className="accordian-title">{data.topicName}</span>
                </AccordionHeader>
                <AccordionBody accordionId={(i+1).toString()}>
                  <ul>
        { !!data?.module && data.module.map((datas,m) => (
                    <li className="list-item" key={m.toString()}>
                      <div className="row">
                        <div className="col-lg-8">
                          <div className="row">
                              <div className="left-title col-lg-6">
                                 {datas.moduleName} 
                              </div>
                              <div className="right-text col-lg-6">
                              {datas.qbCount}  MCQs (034 MCQs + OIBQ)
                              </div>
                          </div>
                          <Form>
                            <FormGroup check inline>
                              <Input type="checkbox" name='' onChange={e => handleChange(e)}   />
                              <Label check>Enable Learning Mode</Label>
                            </FormGroup>
                          </Form>
                        </div>
                        <div className="col-lg-4">
                          {/* <Button className="start-exam">Start Exam</Button> */}
         
                          <Link className="start-exam" to={ '/dashboard/anatomy-question-details/'+ datas.moduleId}>
                          Start Exam
                         
                         
                             </Link>
                        </div>
                      </div>
                    </li>
        ))}
                   
                  </ul>
                </AccordionBody>
              </AccordionItem>))
               : 'no data found' }
            
      </UncontrolledAccordion>
    </div>
  );
};
const mapSateToProps = (state)=>({
  authState: state.authReducer,
  qstnState: state.dashBoardAnatomyReducer
})
const mapDispatchToProps = (dispatch)=>({
  fetchQB_Anatomys: (body)=>dispatch(getQB_Anatomy(body))
})
export default connect(mapSateToProps,mapDispatchToProps)(Anatomy);