import React from 'react'
import infoIcon from 'assets/Information.png'
import { Button, ButtonGroup } from "reactstrap"

function PlanCard(props) {
  return (
    <>

      <div className='card shadow rounded-0 text-center m-4 planCard-section'>
        <div className='bg-light-blue p-3'>
          <h4><b>{props.cardTitle}</b> <img src={infoIcon} alt='' className='ms-3' /></h4>
          <div>{props.subTitle}</div>
        </div>
        <div className='p-5'>
          <h5 className='text-start mb-3 ps-2'>Choose super speaciality</h5>
          <div class="row">
            <div class="col-lg-12">
              <div class="button-group">
                <button type="button" class="btn btn-default btn-sm dropdown-toggle" data-toggle="dropdown" >select <span class="caret"></span></button>
                <ul class="dropdown-menu">
                  <li className='plan-check'><input type="checkbox" />&nbsp;Nerulogy</li>
                  <li className='plan-check'><input type="checkbox" />&nbsp;Nephrology</li>
                  <li className='plan-check'><input type="checkbox" />&nbsp;Medical Gastroenterology</li>
                  <li className='plan-check'><input type="checkbox" />&nbsp;Cardiology</li>
                  <li className='plan-check'><input type="checkbox" />&nbsp;Medical Oncology</li>
                  <div className='text-center'><Button color='blue' className='mt-2 px-5 text-center'>Add</Button></div>
                </ul>
               
              </div>
            </div>
          </div>


          <h6 className='text-start mt-3 mb-3'><span style={{ color: "blue" }}>Default Subject :</span>Surgical Group Subjects</h6>
          <div className='row'>
            <div className='col-6'>
              <div class="form-check  text-start mb-3">
                <input class="form-check-input " type="radio" name="flexRadioDefault" id="flexRadioDefault1" />
                <label class="form-check-label" for="flexRadioDefault1">
                  6 Months
                </label>
              </div>
              <div class="form-check  text-start mb-3">
                <input class="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault2" checked />
                <label class="form-check-label" for="flexRadioDefault2">
                  12 Months
                </label>
              </div>
              <div class="form-check  text-start mb-3">
                <input class="form-check-input " type="radio" name="flexRadioDefault" id="flexRadioDefault1" />
                <label class="form-check-label" for="flexRadioDefault1">
                  18 Months
                </label>
              </div>
              <div class="form-check  text-start mb-3">
                <input class="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault2" checked />
                <label class="form-check-label" for="flexRadioDefault2">
                  24 Months
                </label>
              </div>
            </div>
            <div className='col-6'>
              <div className='d-flex justify-content-center align-items-center mb-2'>
                <h5> 5999</h5> <h3 className='ms-3'></h3> <h5 className='text-gray ms-0 text-line'> 6999</h5>
              </div>

              <div className='d-flex justify-content-center align-items-center mb-2'>
                <h5> 7999</h5> <h3 className='ms-3'></h3> <h5 className='text-gray ms-0 text-line'> 8999</h5>
              </div>

              <div className='d-flex justify-content-center align-items-center mb-2'>
                <h5> 9999</h5> <h3 className='ms-3'></h3> <h5 className='text-gray ms-0 text-line'> 11999</h5>
              </div>

              <div className='d-flex justify-content-center align-items-center mb-2'>
                <h5> 12999</h5> <h3 className='ms-3'></h3> <h5 className='text-gray ms-0 text-line'> 14999</h5>
              </div>

            </div>
          </div>
          <div class="form-group">
            <div className='col-12'>
              <input type="email" className="form-control mt-3 mb-3" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Apply Coupon Code" onChange={props.onChange} />
            </div>

            <button type="submit" class="btn btn-outline-primary btn-circle btn-xl">Apply</button>


          </div>

          <div className='row'>
            <div className='col-8'>

              <h6>Your Total Amount is : 7999</h6>

            </div>
          </div>
          <Button color='blue' className='mt-5 px-5'>Subscribe</Button>
        </div>
      </div>
    </>
  )
}

export default PlanCard