import { Input as ReactInput } from 'reactstrap'

export const Input = ({ className, inputClassName, error, touched, invalid, valid, children, ...props }) => {
    return (
        <div className={className}>
            <ReactInput {...props} className={`p-3 rounded-3 shadow-none ${error && touched ? 'border-danger' : ''} ${inputClassName || ''}`} >{children}</ReactInput>
            {error && touched && <div className='text-danger'>{error}</div>}
        </div>
    )
}