
import gridicon from 'assets/Grid View.png';
import listicon from 'assets/list.png';
import { useEffect } from 'react';
import { useState } from 'react';
import { connect } from 'react-redux';
import {getquestionBanks} from './../../store/dashbaord/questionBank/action';
import ReactPlayer from 'react-player'
import {AiFillCloseCircle, AiFillPlayCircle, AiFillQqSquare} from 'react-icons/ai';
import { useRouteMatch, Link } from 'react-router-dom'

export const QuestionBank = (props) => {
    const { path } = useRouteMatch()
    const [gridView, setGridView] = useState(true);
    const toggleView = () => setGridView(!gridView);
    const {fetchquestionBanks,authState,qstnState } = props;

    const fetchquestionBankList = ()=>{
        const body={
            courseId: authState.profileInfo.courseId,
            studentId: authState.profileInfo.studentId,
            qrp: "false"
        }
        fetchquestionBanks(body);
    }

    useEffect(()=>{
        console.log(authState);
        if(authState.isAuthenticated){
            fetchquestionBankList();
        }
        console.log('qstnState useEffect', qstnState); 

    },[authState.isAuthenticated]);



    return (
    
        <>
         {/* <div className="question-bank-wrapper">
            <h2>Question Bank</h2> */}
        <div className={gridView && false ? 'gridView-videos' : 'listView-videos'}>
                <div className="row mb-2 align-itmes-center">
                    <div className="col-lg-6 col-xl-6 col-md-6 col-sm-6 col-6">
                    <h2>Question Bank</h2>
                    </div>
                    <div className="col-lg-6 col-xl-6 col-md-6 col-sm-6 col-6 text-end">
                        <a onClick={toggleView} id='listiconBtn'><img src={gridView ? listicon : gridicon} /></a>
                    </div>
                </div>
                {gridView ? (
                <div className="row">
                    { !!qstnState?.QBInfo &&  qstnState.QBInfo?.subjects  ? 
                    !!qstnState?.QBInfo && qstnState.QBInfo.subjects.map((question,i)=>(
                        <div className="questionCard-wrapper col-xl-3 col-lg-3 col-md-3 col-sm-3 col-xs-12 col-3" key={i.toString()}>
                        <div className="row questionCard">
                            <div className="qstn-image text-center col-xl-6 col-xs-6 align-self-center">
                             <Link to={ path+  '/'+ question.subjectId}>
                        <img src={question.icon} width="100" />
                             </Link>
                            </div>
                            <div className="qstn-detail col-xl-6 col-xs-6 text-center">
                                <div className="title">
                                {question.name}  
                                </div>
                                <div className="anatomy-complete">
                                {question.inCompleteQbCount+ '/' + question.qbCount}
                                </div>
                                <div>
                                  Questions 1
                                </div>
                            </div>
                        </div>
                    </div>
                    ))
                    : 'no data found' }
                    
                </div>
                ) : (<>
                    {!!qstnState?.QBInfo && qstnState.QBInfo.subjects.map((question)=>(
                    <div className='video-card p-2'>
                        <div className='row align-items-center'>
                            <div className='col-auto'>
                            
                                <div className='vthumbnail2 border border-light relative'>
                                    <div className='center opacity-50 pointer'>
                                        <AiFillQqSquare style={{fontSize: "30px"}}  />
                                        <img src={question.icon} width="100" height="100" />
                                    </div>
                                    
                                </div>
                            </div>
                            <div className='col'>
                                <p className='mb-1 mt-0 p-0'>{question.name}</p>
                                <p className='mb-1 mt-0 p-0'><small>{question.name}</small></p>
                                <p className='mb-1 mt-0 p-0'>{question.name}</p>
                            </div>
                        </div>
                        </div>
                    ))}
                    
                    
                </>)}
            </div>
        </>
    )
}

const mapSateToProps = (state)=>({
    authState: state.authReducer,
    qstnState: state.dashBoardQBReducer,
    
})
const mapDispatchToProps = (dispatch)=>({
    fetchquestionBanks: (body)=>dispatch(getquestionBanks(body))
})
export default connect(mapSateToProps,mapDispatchToProps)(QuestionBank);