import React from 'react'
import {BsFillCaretRightFill} from 'react-icons/bs'

function GrandText() {
  return (
    <div>
    <div className='row mt-5'>
       <div className='col-lg-8 col-xl-8 col-md-12 col-sm-12 col-12'>
           <form>
              
              
               <div className='row align-items-center'>
                   <div className='col-6'>
                   <div className="mb-3 form-group">
                           
                       <label className="form-check-label" for="exampleCheck1"><b>General Embryology</b> &nbsp; &nbsp; 03MCQ<sub>s</sub> (34MCQ<sub>s</sub>+OIBQ) </label>
                       </div>
                       <div className="mb-3 form-check">
                           <input type="checkbox" className="form-check-input" id="exampleCheck1" checked></input>
                           <label className="form-check-label" for="exampleCheck1">Subscribe to Emails</label>
                       </div>
                   </div>
                   <div className='col-6 text-end'>
                       <button type='submit' className='btn btn-primary roundedcorner powerpack'>Start Exam <BsFillCaretRightFill /></button>
                   </div>
               </div>
               <hr/>
               <div className='row align-items-center'>
                   <div className='col-6'>
                   <div className="mb-3 form-group">
                           
                       <label className="form-check-label" for="exampleCheck1"><b>General Embryology</b> &nbsp; &nbsp; 03MCQ<sub>s</sub> (34MCQ<sub>s</sub>+OIBQ) </label>
                       </div>
                       <div className="mb-3 form-check">
                           <input type="checkbox" className="form-check-input" id="exampleCheck1" checked></input>
                           <label className="form-check-label " for="exampleCheck1">Subscribe to Emails</label>
                       </div>
                   </div>
                   <div className='col-6 text-end'>
                       <button type='submit' className='btn btn-primary roundedcorner powerpack'>Start Exam <BsFillCaretRightFill /></button>
                   </div>
               </div>
           </form>
       </div>
   </div>
</div>
  )
}

export default GrandText