import { Button } from "reactstrap";
import { useState } from "react";
const tabs = ["Subject Test", "INI CET", "Grand Test"];
const years = [2022,2021,2020,2019,2018];
const list = [
  {
    name: "Anatomy1",
  },
  {
    name: "Anatomy1",
  },
  {
    name: "Anatomy1",
  },
  {
    name: "Anatomy1",
  },
  {
    name: "Anatomy1",
  },
  {
    name: "Anatomy1",
  },
];

export const TestSeries = () => {
  const [selectedTab, setSelectedTab] = useState(0);
  return (
    <div className="test-series-wrapper">
      <div className="row">
        <div className="col-lg-12">
          <h3>
            <b>Test Series</b>
          </h3>
        </div>
        <div className="col-lg-12 tab-wrapper">
          <div className="row">
            <div className="col-lg-10">
              <ul className="profile-nav">
                {tabs.map((item, i) => (
                  <li key={i}>
                    <a
                      role="button"
                      className={selectedTab === i ? "active" : ""}
                      onClick={() => setSelectedTab(i)}
                    >
                      {item}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
            <div className="col-lg-2">
              <select name="years" id="years">
                {years.map((data,i) => ( <option value={data}>{data}</option>))}
              </select>
            </div>
          </div>
        </div>
        {list.map((data, i) => (
          <div className="col-lg-5 anatomy-wrapper">
            <div className="row">
              <div className="col-lg-3 align-self-center">
                <div className="no-of-questions text-center">
                  <div className="no">
                    12<span>th</span>
                  </div>
                  <div>January</div>
                </div>
              </div>
              <div className="col-lg-6 details align-self-center">
                <div className="heading">
                  <b>Anatomy-2</b>
                </div>
                <div className="date">2 Test Available</div>
              </div>
              <div className="col-lg-3 align-self-center text-center btn-wrapper">
                <Button className="start">{">"}</Button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};
