function PaginatedItems({ itemsPerPage, itemsarrays }) {
  console.log("PaginatedItems itemsarray ", itemsarrays);
   totalMCQ = itemsarrays.length ; 
  // We start with an empty list of items.
  const [currentItems, setCurrentItems] = useState(null);
  const [pageCount, setPageCount] = useState(0);
  // Here we use item offsets; we could also use page offsets
  // following the API or data you're working with.
  const [itemOffset, setItemOffset] = useState(0);

  useEffect(() => {
    // Fetch items from another resources.
    const endOffset = itemOffset + itemsPerPage;
    console.log(`Loading items from ${itemOffset} to ${endOffset}`);
    setCurrentItems(itemsarrays.slice(itemOffset, endOffset));
    setPageCount(Math.ceil(itemsarrays.length / itemsPerPage));
  }, [itemOffset, itemsPerPage]);

  // Invoke when user click to request another page.
  const handlePageClick = (event) => {
    const newOffset = (event.selected * itemsPerPage) % itemsarrays.length;
    console.log(
      `User requested page number ${event.selected}, which is offset ${newOffset}`
    );
    setItemOffset(newOffset);
  };

  return (
    <>
      <Items currentItems={currentItems}  itemsPerPage={itemOffset + itemsPerPage} totalMCQ={totalMCQ}/>
      <div className="pagination">
          <nav class="" aria-label="pagination">
            <ReactPaginate
        breakLabel=""
        className='pagination'
        nextLabel=" >>"
        onPageChange={handlePageClick}
        pageRangeDisplayed={5}
        pageCount={pageCount}
        previousLabel="<<"
        renderOnZeroPageCount={null}
      /> </nav>
       </div>
    </>
  );
}

const mapSateToProps = (state)=>({
  authState: state.authReducer,
  qstnState: state.dashBoardAnatomyReducer,
  items: state.dashBoardAnatomyReducer
})
const mapDispatchToProps = (dispatch)=>({
  fetchQB_Anatomys: (body)=>dispatch(getQB_AnatomyExam(body)),
  submitQB_Anatomys: (body)=>dispatch(submitQB_Anatomys(body))
})
export default connect(mapSateToProps,mapDispatchToProps)(AnatomyExam);