import { useEffect,useState } from "react";
import { connect, useDispatch } from "react-redux";
import { axiosRequestSilent } from 'store/apis/api-v1';
import { toast } from 'react-toastify';
import country from './../../assets/json/country.json';
import { Input } from "components/Input";
import {syncProfile} from './../../store/authentication/action';
import { mobile, phoneNumber1 } from "utils/validators";

export const PhoneNumber = (props) => {

    const [step,setStep] = useState(0);
    const [loading,setLoading] = useState(false);
    const [error,setError] = useState();
    const dispatch = useDispatch();

    const [formState,setFormState] = useState({
        studentId: props.authState?.profileInfo?.studentId,
        currCountryCode: props.authState?.profileInfo?.countryCode,
        currMobileNumber: props.authState?.profileInfo?.mobileNo,
        newMobileNo: "",
        newCountryCode: 91,
        otp: "",
        otpToken:""
    })


    const handleChange = (e)=>{
        if(e.target.name == "newMobileNo"){
            setError(phoneNumber1(e.target.value))
        }
        setFormState({
            ...formState,
            [e.target.name]: e.target.value
        })
    }

    const getOTP = async ()=>{
        setLoading(true);
        try {
            const res = await axiosRequestSilent("POST","/student","/update/updateMobileSendOTP",{studentId: formState.studentId},{})
            if(res.data?.gatewayResponse?.status?.isSuccess){
                toast.success("OTP sent to current number.");
                setFormState({
                    ...formState,
                    otpToken: res.data?.gatewayResponse.response.otpToken
                })
                setStep(1);
            }else{
                toast.error(res.data?.gatewayResponse?.status?.message);
            }
        } catch (error) {
           console.log(error);
        }finally{
            setLoading(false);
        }
    }

    const veryOTPAndUpdateNumber = async ()=>{
        if(!!error) return;
        setLoading(true);
        const body = {
            studentId: formState.studentId,
            otpToken: formState.otpToken,
			newCountryCode: formState.newCountryCode,
			newMobileNo: formState.newMobileNo,
			otp: formState.otp,
        }
        try {
            const res = await axiosRequestSilent("POST","/student","/update/mobileNo",body,{});
            if(res.data?.gatewayResponse?.status?.isSuccess){
                toast.success("Number updated .");
                setStep(0);
                setFormState({
                    ...formState,
                    currCountryCode: formState.newCountryCode,
                    currMobileNumber: formState.newMobileNo,
                    newCountryCode: "",
                    newMobileNo: "",
                    otp: "",
                    otpToken: ""
                })
                dispatch(syncProfile());
            }else{
                toast.error(res.data?.gatewayResponse?.status?.message);
            }
        } catch (error) {
            console.log(error);
        }finally{
            setLoading(false);
        }
    }

    const handleSubmit = (e)=>{
        e.preventDefault();
        switch (step) {
            case 0:
                getOTP();
                break;
            case 1:
                veryOTPAndUpdateNumber();
                break;
            default:
                break;
        }
    }

    return (
        <div className='row mt-5'>
            <div className='col-lg-8 col-xl-8 col-md-12 col-sm-12 col-12'>
                <form onSubmit={handleSubmit}>
                    <div className='form-group'>
                        {step == 0 && <div className="row">
                            <div className="col-3">
                                <Input type={"select"} disabled value={formState.currCountryCode} name={"newCountryCode"} onChange={handleChange}  required>
                                    <option value={"91"}>+91</option>
                                    {Object.keys(country).sort((a,b)=>a-b).map((item)=>(
                                        <option value={country[item][1]}>
                                            +{country[item][1]}
                                        </option>))
                                    }
                                </Input>
                            </div>
                            <div className="col-9">
                                <Input disabled type='text' t value={formState.currMobileNumber} onChange={handleChange}  placeholder="Enter New Phone number" required></Input>
                            </div>
                        </div>}
                        {step == 1 && <div className="row">
                            <div className="col-3">
                                <Input type={"select"}  name={"newCountryCode"} onChange={handleChange}  required>
                                    <option value={"91"}>+91</option>
                                    {Object.keys(country).sort((a,b)=>a-b).map((item)=>(
                                                    <option value={country[item][1]}>
                                                        +{country[item][1]}
                                                    </option>))
                                    }
                                </Input>
                            </div>
                            <div className="col-9">
                                <Input inputmode="numeric"  onKeyPress={(event) => {
        if (!/[0-9]/.test(event.key)) {
          event.preventDefault();
        }
      }}  maxlength="10" name={"newMobileNo"} onChange={handleChange}  placeholder="Enter New Phone number" required></Input>
                                <p className="sm-font text-danger my-1">{error}</p>
                            </div>
                        </div>}
                    </div>
                    {step == 1 && <div className='form-group'>
                        <Input inputmode="numeric"  onKeyPress={(event) => {
        if (!/[0-9]/.test(event.key)) {
          event.preventDefault();
        }
      }} maxlength="4"    name={"otp"} onChange={handleChange}  placeholder="Enter OTP" required></Input>
                    </div> } 
                    {step == 0 && <div className='form-group text-end'>
                        <button type='submit' className='btn btn-primary roundedcorner'>Add New Number</button>
                    </div>}                             
                    {step == 1 && <div className='form-group text-end'>
                        <button type='submit' className='btn btn-primary roundedcorner' disabled={!!error || loading}>Save</button>
                    </div> }                            
                </form>
            </div>
        </div>          
    )
}

const mapStateToProps= (state)=>({
    authState: state.authReducer
});

export default connect(mapStateToProps,null)(PhoneNumber);
