import { Sidebar } from "components/Dashboard/Sidebar"
import  Plan  from "./Plan"
import { Redirect } from "react-router-dom"
import { Route, Switch, useRouteMatch   } from "react-router-dom"
import Anatomy  from "./anatomy"
import  AnatomyExam  from "./anatomyExam"
import AnatomyQuestionDetails  from "./anatomyQuestionDetails"
import { Bookmark } from "./Bookmark"
import CustomeModule from "./CustomeModule"
import ExamReview  from "./examReview"
import  Main  from "./Main"
// import { QuestionBank } from "./questionBank"
import  QuestionBank from "./questionBank"
import  Videos  from "./videos"
import { TestSeriesanatomyList } from "./TestSeriesAnatomyList"
import { TestSeries } from "./TestSeries"
import { TestInstructions } from "./TestInstructions"
import { TestSeriesAnatomyInfo } from "./TestSeriesAnatomyInfo"
import { TestSeriesQuestion } from "./TestSeriesQuestion"
import { TestSeriesReview } from "./TestSeriesReview"

export const Dashboard = () => {
    const { path } = useRouteMatch()
    const URL=window.location.href;
    return (
        <div className="dashboard-page">
            <div className="wrapper">
                {URL.includes("dashboard/plan")?"":<Sidebar />}
                <div id="content">
                    <Switch>
                        <Route exact path={path + '/profile'} component={Main} />
                        <Route path={path + '/videos'} component={Videos} />
                        <Route exact path={path + '/question-bank'} component={QuestionBank} />
                        <Route  exact path={path + '/question-bank/:qbid' } component={Anatomy} />
                        {/* <Route path={path + '/anatomy'} component={Anatomy} /> */}
                        <Route path={path + '/anatomy-question-details/:moduleid'} component={AnatomyQuestionDetails} />
                        <Route path={path + '/anatomy-exam/:moduleid'} component={AnatomyExam} />
                        <Route path={path + '/exam-review'} component={ExamReview} />
                        <Route path={path + '/bookmark'} component={Bookmark} />
                        <Route path={path + '/custom-module'} component={CustomeModule} />
                        <Route path={path + '/test-anatomy-list'} component={TestSeriesanatomyList} />
                        <Route path={path + '/test-series'} component={TestSeries} />
                        <Route path={path + '/test-instructions'} component={TestInstructions} />
                        <Route path={path + '/test-series-anatomy-info'} component={TestSeriesAnatomyInfo} />
                        <Route path={path + '/test-series-questions'} component={TestSeriesQuestion} />
                        <Route path={path + '/test-series-review'} component={TestSeriesReview} />
                        <Route path={path + '/plan'} component={Plan} />
                        {/* <Redirect to={path + '/profile'} /> */}
                    </Switch>
                   
                </div>
            </div>
        </div>
    )
}