import { useEffect,useState } from "react";
import { connect } from "react-redux";
import { axiosRequestSilent } from 'store/apis/api-v1';
import { toast } from 'react-toastify';
import { password } from "utils/validators";

export const Password = (props) => {

    const [formState,setFormState] = useState({
        oldPassword: "",
        newPassword: "",
        studentId: ""
    })

    const [error,setError] = useState({oldPassword: "", newPassword:""});

    const [loading,setLoading] = useState(false);

    const handleSubmit =  async (e)=>{
        if(!!error.newPassword || !!error.oldPassword) return;
        e.preventDefault();
        setLoading(true);
        try {
            const res = await axiosRequestSilent("POST","/student","/change/password",formState,{});
            if(res.data?.gatewayResponse?.status?.isSuccess){
                toast.success("Password Updated.")
                setFormState({
                    ...formState,
                    oldPassword: "",
                    newPassword: ""
                })
            }else{
                toast.error(res.data?.gatewayResponse?.status?.message);
            }
        } catch (error) {
            toast.error("Something went wrong");
        }finally{
            setLoading(false);
        }
    }

    useEffect(()=>{
        if(!!props.authState?.profileInfo?.studentId){
            setFormState({
                ...formState,
                studentId: props.authState?.profileInfo?.studentId
            })
        }
    },[]);

    const handleChange = (e)=>{
        setFormState({
            ...formState,
            [e.target.name]:e.target.value
        })
        setError({
            ...error,
            [e.target.name]: password(e.target.value)
        })
    }

    return (
        <div className='row mt-5'>
            <div className='col-lg-8 col-xl-8 col-md-12 col-sm-12 col-12'>
                <form onSubmit={handleSubmit}>
                    <div className='form-group'>
                        <input onChange={handleChange} type={"password"} value={formState.oldPassword}  name={'oldPassword'} className="form-control" placeholder="Old Password" required></input>
                        <p className="sm-font text-danger my-1">{error.oldPassword}</p>
                    </div>
                    <div className='form-group'>
                        <input onChange={handleChange} type={"password"} value={formState.newPassword} name={'newPassword'} className="form-control" placeholder="New Password" required></input>
                        <p className="sm-font text-danger my-1">{error.newPassword}</p>
                    </div>
                    <div className='form-group text-end'>
                        <button
                         disabled={(!!error.newPassword || !!error.oldPassword) || loading}
                         type='submit'
                         className='btn btn-primary roundedcorner'>
                            Save
                        </button>
                    </div>                             
                </form>
            </div>
        </div>
    )
}
const mapStateToProps= (state)=>({
    authState: state.authReducer
});

export default connect(mapStateToProps,null)(Password);
