
import { useState } from 'react';
import { ButtonGroup, Button } from 'reactstrap'
import { Carousel } from 'react-responsive-carousel';
import prevIcon from 'assets/Previeus.png'
import instagram from 'assets/instagram.png'
import { useEffect } from 'react';

const getSubArrayData = (arr,num=3)=>{
    let length = arr.length;
    let i = 0;
    let res = [];
    while(i < length){
        let currArray = arr.slice(i,i+3);
        res.push(currArray);
        i= i+3;
    }
    return res;
}

export const DocTutorialCommunity = (props) => {
    const [selected, setSelected] = useState(props.testimonials.map((item)=>item.name)[0])
    const [listButton,_] = useState([...props.testimonials.map((item)=>item.name)]);
    const [currTestimonials,setCurrentTestimonials] = useState([]);
    
    useEffect(()=>{
        let plain = [];
        props.testimonials.map((item)=>{
            if(item.name == selected){
                plain = item.testimonials;
            }
        })
        plain = getSubArrayData(plain,3)
        setCurrentTestimonials(plain);
    },[selected]);

    return (
        <div className='mt-5' id="review">
            <h1 className='font-medium text-center text-md-left'>DocTutorials Community</h1>
            <div className='text-center mt-5'>
                <ButtonGroup className='bg-light-gray shadow'>
                    {listButton.map((item, i) => <Button key={i} onClick={() => setSelected(item)} color={selected === item ? 'primary' : 'link'}>{item}</Button>)}
                </ButtonGroup>
            </div>

            <div className='d-none d-md-block'>
                <Carousel showThumbs={false} showArrows infiniteLoop>
                    {currTestimonials.map((itemP,i)=>(
                        <div className='row d-flex mt-5' key={i}>
                            {itemP.map((item)=>(
                            <div className='m-auto col mb-5 col-md-4' key={i+Math.random()}>
                                <div className='card shadow p-3 bg-light-blue'>
                                    <div className='text-center'>
                                        <img src={item.image} alt='' className='avatar' />
                                        <h5 className='mt-2'><b>{item.name ? item.name: 'NA'}</b></h5>
                                        <div className='rank'>
                                            <h4>AIR<br />{item.rank}</h4>
                                        </div>
                                    </div>
                                    <div>{item.text.substring(0,400) + "..."}</div>
                                </div>
                            </div>
                            ))}
                        </div>
                    ))}
                </Carousel>
            </div>
            <div className='d-md-none'>
                <Carousel showThumbs={false} infiniteLoop dynamicHeight={false} renderArrowPrev={(onClick) => <img src={prevIcon} onClick={onClick} alt='' className='prev-btn' />}>
                {currTestimonials.map((itemP,i)=>(
                    <div className='m-5 mb-3' key={i}>
                        {itemP.map((item,i)=>(
                            <div className='card shadow p-3 bg-light-blue' key={"i"+ i}>
                            <div className='text-center'>
                                <img src={instagram} alt='' className='avatar' />
                                <h5 className='mt-2'><b>{item.name}</b></h5>
                                <div className='rank'>
                                    <h4>AIR<br />{item.rank}</h4>
                                </div>
                            </div>
                            <div>{item.text}</div>
                        </div>
                        ))}
                    </div>
                ))}
                </Carousel>
            </div>
        </div>
    )
}