import AllBookmark from "./AllBookmark"
import GrandText from "./GrandText"
import { useState } from "react"
import { BsSearch } from 'react-icons/bs'
import { BsCaretDownFill } from 'react-icons/bs'

const tabs = ['all', 'Bookmarked', 'Not Bookmarked']
const tabs2 = ['Exam Mode', 'Learning Mode']
function CustomeModule() {
    const [selectedTab, setSelectedTab] = useState(0)
    const [selectedTab2, setSelectedTab2] = useState(0)

    return (
        <>
            <div className="row mb-2 align-itmes-center">
                <div className="col-12 mb-5">
                    <h3><b>Custom Module</b></h3>
                </div>
                <div className='col-6'>
                    <h5 className="mb-3">Create Form</h5>
                    <ul className='custom-module-profile-nav'>
                        {tabs.map((item, i) => (<li key={i}><a role='button' className={selectedTab === i ? 'active' : ''} onClick={() => setSelectedTab(i)}>{item}</a></li>))}
                    </ul>
                </div>
                <div className="col-6">
                    <div class="input-group">
                        <div className='col-6 text-center mt-1'>
                            <h5 className="mb-3">Topic Selection</h5>
                            <div class="dropdown show">
                                <button type='submit' className='btn dropdown-toggle cust-btn' data-toggle="dropdown"><span style={{ paddingRight: "70px" }} >All</span>
                                </button>
                                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                    <a class="dropdown-item" href="#">All</a>
                                    <a class="dropdown-item" href="#">All</a>
                                    <a class="dropdown-item" href="#">ALl</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='col-6 mt-5'>
                    <h5 className="mb-3">Mode</h5>
                    <ul className='custom-module-profile-nav mode'>
                        {tabs2.map((item, j) => (<li key={j} ><a role='button' className={selectedTab2 === j ? 'active' : ''} onClick={() => setSelectedTab2(j)}>{item}</a></li>))}
                    </ul>
                </div>

                <div className="col-6 mt-5">
                    <div class="input-group">

                        <div className='col-6 text-center mt-1'>
                            <h5 className="mb-3">Number of Questions</h5>
                            <div class="dropdown show">
                                <button type='submit' className='btn dropdown-toggle cust-btn' data-toggle="dropdown"><span style={{ paddingRight: "70px" }} >10</span>
                                </button>
                                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                    <a class="dropdown-item" href="#">20</a>
                                    <a class="dropdown-item" href="#">30</a>
                                    <a class="dropdown-item" href="#">40</a>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

            </div>
            <div className='col-6 text-end mt-5 pt-5'>
                <button type='submit' className='btn' style={{ borderRadius: "50px", boxShadow: "1px 1px 5px 4px rgba(0,0,0,0.17)" }}>Generate Test  </button>
            </div>
        </>
    )
}



export default CustomeModule